
.vui.layout.demo {
  text-align: center;
  color: #ffffff;
  margin-bottom: 20px;
  font-weight: bold;
}
.vui.layout.demo .header, .vui.layout.demo .footer{
  background: #7dbcea;
  color: #fff;
}
.vui.layout.demo .sider {
  background: #3ba0e9;
}
.vui.layout.demo .content {
  background: rgba(16, 142, 233, 1);
  line-height: 120px;
}
.logo {
  padding: 0.7em .8em;
  font-weight: bold;
  font-size: 1.2rem;
}
.vui.layout.demo2 > .header {
  padding: 0 20px;
  background: #e0f0fa;
  margin-bottom: 20px;
}
.vui.layout.demo2 > .content {
  margin: 0 20px;
  padding: 0.7em 1em;
}
.vui.layout.demo3 > .header {
  padding: 0;
  background: #e0f0fa;
}
.vui.layout.demo3 .has-sider > .content {
  margin: 20px;
  padding: 0.7em 1em;
}
.vui.layout.demo3 .logo {
  padding: 1em;
}



