/** animate **/
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes zoom-in {
  0% {
    opacity: 0;
    transform: scale(0.2);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes zoom-out {
  0% {
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(0.2);
  }
}

@keyframes move-in {
  0% {
    opacity: 0;
    transform: translate3d(0, -1rem, 0);
  }
  to {
    transform: none;
  }
}
.move-in {
  animation-name: move-in;
  -webkit-animation-duration: .3s;
  animation-duration: .3s
}

.tag-enter-active {
  -webkit-animation: fade-in .2s cubic-bezier(.78,.14,.15,.86);
  animation: fade-in .2s cubic-bezier(.78,.14,.15,.86);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both
}

.tag-leave-active {
  -webkit-animation: zoom-out .3s cubic-bezier(.78,.14,.15,.86);
  animation: zoom-out .3s cubic-bezier(.78,.14,.15,.86);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both
}



