@import "./base.less";

.vui.dimmer {
  display: none;
  position: absolute;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.06);
  opacity: 0;
  line-height: 1;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  .transition(background-color 0.5s linear);
  .user-select(none);
  will-change: opacity;
  z-index: 1000;

  .loading(2em);

  &.active {
    display: block;
    opacity: 1;
  }
}



