
.main-layout > .vui.layout {
  background: #f0f2f5;
  color: #666;
}
.main-layout > .vui.layout > .header{
  /*background: #7dbcea;*/
  padding: 0 50px;
  /*background: #666;*/
}
.main-layout > .vui.layout > .header {
  margin-bottom: 20px;
}
.main-layout > .vui.layout > .sider {
  /*background: #fff;*/
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-layout > .vui.layout > .has-sider {
  margin: 0 50px;
}
.main-layout > .vui.layout > .content {
  /*background: rgba(16, 142, 233, 1);*/
  padding: 20px;
}
.hljs {
  padding: 0;
  background-color: #ffffff;
  margin: 0;
}
code, pre {
  tab-size: 2;
  padding: 0;
  margin: 0;
}
table code, ul li code {
  margin: 0 1px;
  background: #f7f7f7;
  padding: .2em .4em;
  border-radius: 3px;
  font-size: .9em;
  border: 1px solid #eee;
}
ul.api li {
  margin: 10px;
}
.vui.table.attributes table thead tr th:first-child {
  width: 16%;
}
.vui.table.attributes table thead tr th:nth-child(3) {
  width: 20%;
}
.vui.table.attributes table thead tr th:nth-child(4) {
  width: 10%;
}



